h1, h2, h3, h4, h5, h6 {
	clear: both;
	margin-top: 0;
}
p+h2{
	margin-top: -20px;
}

h2{
	font-weight: 600;
	font-size: 4.8rem;
	@include line-height(52,48);
	margin-bottom: 27px;
	text-transform: uppercase;

	&:after{
		content: '';
		display: block;
		width: 58px;
		height: 2px;
		background-color: #fff;
		margin-top: 23px;
	}


	&.has-text-align-center{
		&:after{
			margin-left: auto;
			margin-right: auto;
		}
	}
}