.wp-block-cover.up-negative-margin{
    margin-top: -150px;

    p{
        margin: 0;
        margin-top: 150px;
    }
}

.both-title-border{
    margin-bottom: 0;

    &:before,&:after{
        content: '';
        display: block;
        height: 7px;
        width: 80%;
        background-color: $color_block_palet1;
        border-radius: 5px;
    }

    &:before{
        margin-bottom: 30px;
    }

    &:after{
        margin-top: 30px;
    }
    
}

.bottom-title-border{
    &:after{
        content: '';
        display: block;
        height: 7px;
        width: 80%;
        background-color: $color_block_palet1;
        border-radius: 5px;
        margin-top: 30px;
    }
}

.wp-block-columns{
    margin-bottom: 0;
}

.wp-block-spacer{
    @include media-screen($bk_mobile){
        max-height: 50px;
    }
}

.add-special-dot{
    &:after{
        content: '...';
        display: block;
        color: $color_block_palet1;
        @include line-height(23,36);
        font-size: 3.6rem;
    }
}

.entry-content .wp-block-columns{
    padding: 0;
}

.entry-content .wp-block-columns p{
    
    &.has-text-align-center{
        max-width: 400px;
        margin: 0 auto;
    }
}

.wp-block-columns .wp-block-image{
    height: 480px;
   
    img{
        width: 100%;
        height: 100%;
        object-fit: cover;
        opacity: 0.8;
    }
}
