.banner{
    overflow: hidden;

    figure{
        height: 100vh;
        max-height: 900px;

        @include media-screen(1400px){
            height: 800px;
        }

        @include media-screen(1000px){
            height: 650px;
        }

        @include media-screen($bk_mobile){
            height: 500px;
        }
    }

    .svg-wrapper{
        position: absolute;
        top: 200px;
        z-index: 5;
        left: 0;
        right: 0;
        text-align: center;
        bottom: 0;

        svg{
            margin: 0 auto;
            height: 100%;
        }
    }

    .content-on-image{
        position: absolute;
        bottom: 30%;
        left: 0;
        right: 0;
        z-index: 10;
        text-align: center;
        color: #fff;
    }
}