#masthead{
    background-color: transparent;
    top: 0;
    left: 0;
    right: 0;
    z-index: 100;

    ///
    /// @at-root pour si .admin-bar est présent, on décalle le fixed du sticky pour ne pas superposer le header et le menu wp.
    ///
    @at-root .admin-bar &{
        top: 32px;
    }

    @include media-screen(782px){
        @at-root .admin-bar &{
            top: 46px;
        }
    }

    @include media-screen(600px){
        @at-root .admin-bar &{
            top: 0px;
        }
    }
}

.navbar-wrapper{
    position: relative;
    background-color: transparent;
    transition: background 0.5s ease;
    box-shadow: 0 0 3px -1px rgba(0,0,0,.75);
   
    .content{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-top: 15px;
        padding-bottom: 15px;

        @include media-screen(1200px){
            padding-top: 25px;
            padding-bottom: 25px;
        }

        @include media-screen($bk_mobile){
            padding-top: 20px;
            padding-bottom: 20px;
        }

    }

    // logo 
    .site-branding{
        .site-logo{
            display: inline-block;

            img{
                max-height: 80px;
            }
        }
    }

    //nav
    .main-navigation{
        @include media-screen($bk_mobile){
            position: absolute;
            top: 100%;
        }
        
        .menu{
            display: flex;
            
            li{
                margin-right: 25px;

                &:last-child{
                    margin-right: 0;
                }
            }

            a{
                font-weight: 700;
                color: $color_block_palet2;
                font-size: 1.6rem;

                &:hover{
                    color: $color_block_palet1;
                }
            }
        }
    }
    // burger icon
    .menu-toggle{
        display: none;
        border: 0;
        border-radius: none;
        background: none;
        outline: none;
        padding: 0;

        &:before{
            content: none;
        }

        @include media-screen($bk_mobile){
            display: block;
        }

        i{
            font-size: 3rem;
            color: $color_block_palet2;
            transition: color 0.5s ease;

            &:hover{
                color: $color_block_palet1;
            }
        }
    }
}