.button,
button,
input[type="button"],
input[type="reset"],
input[type="submit"],
.wp-block-button__link {
	font-size: 1.8rem;
	color:#fff;
	font-weight: 700;
	background: $color_block_palet1;
	line-height: 2.4rem;
	padding: 0.8rem 2.2rem;
	cursor: pointer;
	transition: color 0.5s ease, background-color 0.5s ease;
	display: inline-block;
	border-radius: 5px;

	&:hover {
		background: $color_block_palet3;
		color: #fff;
	}

	&:active,
	&:focus {
		border-color: $color__border-button-focus;
	}

	// &:visited{
	// 	color: inherit;
	// }
}